@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Karla&display=swap);
@import url(https://fonts.googleapis.com/css?family=Karla&display=swap);
@import url(https://fonts.googleapis.com/css?family=Karla&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #343a40 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav a {
    font-family: 'Montserrat', sans-serif;
}




.AboutSection .lead {
    text-align: center;
}

.AboutSection {
    background-color: aliceblue;
    padding-bottom: 90px;
}

.aboutSubtext {
    font-family: 'Karla', sans-serif;
}

span.boldedText {
    font-weight: bold;
}

.boldedTitle {
    text-align: center;
    font-weight: bold;
    font-size: larger;
}

.profilePicFormatting {
    border-radius: 50%;
    float: right;
    width: 200px;
    height: 200px;
}


.ResumeSection {
    background-color: #bdc5ff;
    margin-top: -10px;
    font-family: 'Karla', sans-serif;
}

.resumeTitle {
    padding-top: 30px;
    font-weight: bold;
    text-align: center;
    font-size: larger;
}

.boldedResumeTitle {
    font-weight: bold;
    border-bottom: 1px solid #787fb3;
}

.logoIcon {
    width: 100px;
    height: 100px;
    margin-left: -95px;
    float: left;
    margin-top: -20px;
}

.rightSection {
    float: right;
}

.logoIconRounded {
    width: 80px;
    height: 80px;
    margin-left: -95px;
    float: left;
    margin-top: -20px;
    border-radius: 50%;
}

.ProjectSection {
    background-color: #f9ecf0;
    margin-top: -10px;
    font-family: 'Karla', sans-serif;
}

.projectPageTitle {
    padding-top: 30px;
    font-weight: bold;
    text-align: center;
    font-size: larger;
}

.card-img-top {
    width: 100%;
    height: 30vw;
    object-fit: cover;
}

